<template>
  <div class="goods-list-container">
      <div class="list-header van-hairline--top-bottom">
        <div :class="{active:type==='all'}" @touchend="changeType('all')">综合</div>
        <div :class="{active:type==='sale'}"  @touchend="changeType('sale')">销量</div>
        <div class="price"
          :class="{'price-up': type==='price-up','price-down': type==='price-down'}"
           @touchend="changeType('price')"
          >
         价格
         </div>
      </div>
      <div class="list-content content" ref='content'>
          <van-pull-refresh v-model="refreshing" @refresh="onRefresh" >
               <van-list
                v-model="loading"
                :finished="finished"
                finished-text="没有更多了"
                @load="onLoad"
               :immediate-check="false"
               >
                    <!-- 把item所有属性都给子元素GoodCard -->
                    <GoodsCard v-for="item in goodList"
                     :key="item.id"
                     v-bind="item"
                     :num="counterMap[item.id]"
                     />

               </van-list>
          </van-pull-refresh>
      </div>
  </div>
</template>

<script>
import { mapState, mapMutations, mapActions } from 'vuex';
import GoodsCard from '@/components/GoodsCard/index.vue';

export default {
  name: 'GoodList',
  components: {
    GoodsCard,
  },
  data() {
    return {
      type: 'all',
      refreshing: false,
      loading: false,
      finished: false,
      page: 1,
      // scrollTop: 0, //content 滚动高度（获取scrollTop方案一）

    };
  },
  computed: {
    ...mapState(['goodList', 'isFinished', 'counterMap']),

  },
  methods: {
    ...mapMutations(['resetGoodList', 'setGoodList']),
    ...mapActions(['getGoodList']),
    changeType(type) {
      if (type === 'all') this.type = 'all';
      else if (type === 'sale') this.type = 'sale';
      else if (this.type === 'price-up') this.type = 'price-down';
      else this.type = 'price-up';
      this.onRefresh('refresh');
    },
    // 下拉刷新
    async onRefresh() {
      this.refreshing = true;

      // 清空列表
      this.resetGoodList();

      // 重新加载数据
      this.finished = false;
      this.onLoad('refresh');
    },
    // 上拉加载 （进行翻页加载: 就是下一页/获取下一页数据）
    async onLoad(refresh) {
      this.finished = false;
      this.page += 1;
      if (refresh === 'refresh') this.page = 1;
      this.loading = true;// 数据加载中

      const hasMoreData = await this.getGoodList({
        page: this.page,
        sort: this.type,
      });
      this.loading = false;
      this.refreshing = false;
      console.log('hasMoreData', hasMoreData);
      if (!hasMoreData) {
        this.finished = true;// 所有数据加载完了， 不会再触发load事件
      }
    },
  },

  watch: {
    // 切换子类目时，初始化数据。
    isFinished() {
      console.log('reset fini--------------------------');
      this.finished = false;
      this.loading = false;
      this.refreshing = false;
      this.page = 1;
    },
  },
  mounted() {
    // 获取scrollTop 方案一 ： 给dom注册scroll事件（绑定一个scroll事件处理函数）
    // console.log('mounted');
    // this.content = this.$refs.content;
    // console.dir(this.content);
    // this.content.addEventListener('scroll', () => {
    //   console.log('scroll');
    //   this.scrollTop = this.content.scrollTop; // 可以拿到scrollTop
    //   console.log(this.scrollTop);
    // });
    // 获取scrollTop 方案二 ： 借用导航守卫 ， 详见 router/index.js
  },
  // 方案三：行不通
  // deactivated() {
  // console.log('deactivated');
  // console.dir(this.$refs.content); //scrollTop 始终为0 ，拿不到scrollTop
  // console.log('scrollTop', this.$refs.content.scrollTop); // 拿不到scrollTop
  // this.scrollTop = this.$refs.content.scrollTop;
  // console.log(this.scrollTop);
  // },
  activated() {
    const { scrollTop } = this.$route.meta;
    const $content = document.querySelector('.content');
    // console.log('activated', scrollTop, $content);
    if (scrollTop && $content) {
      $content.scrollTo({ // 如果直接给dom.scrollTop  赋值 无效。
        top: scrollTop, // 当前采用方案二，  如果是，方案一，只需将此处scrollTop 改写成this.scrollTop
      });
    }
  },
};
</script>

