  position: fixed;
  width:296px;
  left:79px;
  top:135px;

     .list-header{
       box-sizing: border-box;
       top:0px;
       width:296px;
       padding: 0px 8px;
       display: flex;
       justify-content: flex-end;
       &>div{
         position: relative;
         width:50px;
         height: 25px;
         text-align: center;
         line-height: 25px;
         color: #cecece;
          &.active{
             color:#ff1a90;
             font-weight: bold;
           }
          &.price{
            background-color: yellow;
               &::before{
                  content: '';
                  // display: inline-block;
                   border:4px solid transparent;
                   border-bottom-color: #aaa;
                   position: absolute;
                   right:0px;
                   top:4px;

               }
                 &.price-up{
                     color:#ff1a90;
                     font-weight: bold;
                      &::before{
                         border-bottom-color:#ff1a90;
                      }
                  }
               &::after{
                  // display: inline-block;
                   content: '';
                   border:4px solid transparent;
                   border-top-color: #aaa;
                   position: absolute;
                   bottom: 4px;
                   right:0px;

               }
               &.price-down{
                      color:#ff1a90;
                      font-weight: bold;
                      &::after{
                         border-top-color:#ff1a90;
                      }
                  }

          }
        }
     }
     .list-content{

            position:fixed;
            top:170px;
