<template>
   <div class="goods-card-container van-hairline--bottom">
       <div class="card-img">
           <img :src="images[0]" ref='img' >
       </div>
       <div class="card-content">
           <div class="title overflow-hidden">{{title}}</div>
            <div class="desc overflow-hidden">{{desc}}</div>
             <div class="tags">
                 <div v-for="i in tags" :key="i">{{i}}</div>
             </div>
              <div class="prices">¥{{price}}</div>
              <div class="counter">
                  <div  v-if="num" @touchend="counter(id,-1)"><img src="https://duyi-bucket.oss-cn-beijing.aliyuncs.com/img/rec.png"></div>
                  <div class="num" v-if="num">{{num}}</div>
                  <div @touchend="counter(id,1)"><img src="https://duyi-bucket.oss-cn-beijing.aliyuncs.com/img/add.png"></div>
              </div>

       </div>
   </div>
</template>

<script>
import { mapMutations } from 'vuex';
import { animate } from '@/utils';

export default {
  name: 'GoodsCard',
  props: ['images', 'tags', 'title', 'price', 'desc', 'id', 'num'],
  methods: {
    // 更新增减商品的本地存储
    ...mapMutations(['changeCounterStorage']),
    // 更新增减商品的本地存储
    counter(id, num) {
      console.log(id, num);
      this.changeCounterStorage({ id, value: num });
      // 添加商品  动画
      if (num === -1) return;
      console.log('animate');
      const { img } = this.$refs;
      const { left: startX, top: startY } = img.getBoundingClientRect();
      const width = img.offsetWidth;
      const height = img.offsetHeight;
      const cart = document.querySelector('#cart');
      const { left: cartX, top: cartY } = cart.getBoundingClientRect();
      const cartWidth = cart.offsetWidth;
      const cartHeight = cart.offsetHeight;
      const endX = cartX + cartWidth / 2;
      const endY = cartY + cartHeight / 2;
      //   console.log(startX, startY, endX, endY, img.src, width, height);
      animate({
        startX, startY, endX, endY, src: img.src, width, height,
      });
    },
  },
};
</script>

