<template>
  <div class="sidebar-container" ref="sideBar">
      <div class="side-item"
       v-for="(item,i) in sideList"
       :key="item"
       :class="{active: i===index}"
       @touchend="scrollTo(i)"
       @touchstart="move=false"
       @touchmove="move=true"
      >{{ i===0 ? '全部' : item}}
      </div>
  </div>
</template>

<script>
// 从仓库拿数据，渲染。
import { mapState, mapActions, mapMutations } from 'vuex';
// import { sports } from '@/utils';

export default {
  name: 'SideBar',
  data() {
    return {
      index: 0,
      move: false,
      type: null,
    };
  },
  computed: {
    ...mapState(['sideList', 'isFinished']),
  },
  methods: {
    ...mapActions(['getGoodList']),
    ...mapMutations(['resetGoodList', 'setIsFinished']),
    scrollTo(i) {
      if (this.move) return;
      this.index = i;
      // 计算运动距离(纵向滚动)

      const { sideBar } = this.$refs;
      const sideItem = sideBar.children[i];

      const sideBarST = sideBar.scrollTop;
      const sideBarLength = sideBar.offsetHeight;
      const itemHeight = sideItem.offsetHeight;
      const itemTop = sideItem.getBoundingClientRect().top - sideBar.getBoundingClientRect().top;
      const dis = itemTop - sideBarLength / 2 + itemHeight / 2;// item在sideBar中线上侧，dis为负数（滚动条位置减）
      //  ,反之， item在sideBar中线下侧，dis为正数（滚动条位置加）,
      console.log(sideBarST, itemTop, sideBarLength / 2, itemHeight / 2, dis);
      // 方案一 原生H5 API
      sideBar.scrollTo({
        top: sideBarST + dis,
        left: 0,
        behavior: 'smooth',
      });
      // 方案二 JS 动画
      //   sports.moveTo(sideBar, sideBarST, dis, 'scrollTop');

      // 获取列表页数据
      this.resetGoodList();// 切换商品类别前 ，清空商品列表数据/初始化一下
      this.setIsFinished(!this.isFinished);
      console.log(this.sideList[i]);
      this.getGoodList({ type: this.sideList[i], page: 1, sort: 'all' });
    },

  },

  //   watch: {
  //     sideList() {
  //       this.getGoodList({ type: this.sideList[0], page: 1, sort: 'all' });
  //     },
  //   },

  mounted() {
    // 一上来先获取一次商品列表 全部 ,但是，一上来的时候，sideList = []
    // console.log(this.sideList[0]);
    this.resetGoodList();// 侧边栏组件重新装载之前，也清空商品列表数据/初始化一下
    this.getGoodList({ type: this.sideList[0], page: 1, sort: 'all' }); // 拿到sideList数据之后，才装载该组件，但是，
    // 数据仍然可能为空数组 或者其他可能情况，数据来源于后端。
    // 但是，由于v-else的存在，导致
    // （🔥但我把v-else去掉了，为了SideBar 和 GoodsList共用这个loading组件），
    // sideList请求到数据之后，才首次渲染（装载）当前组件SideBar，
    // console.log('139');

    // 这是一个死循环：  组件挂载之后，  然而 ，请求goodList 时 设置loading = true,
    // 引发vue又「销毁」了这个组件，然后，等到goodList请求到了，又vue又「新建」这个组件 ，于是进入死循环
  },
};
</script>

<style lang="less" scoped>
.sidebar-container{
    position: fixed;
    left:0px;
    width: 79px;
    top:135px;
    bottom: 50px;
    overflow: auto;
   background-color:#f8f8f8;
   div{
       width:79px;
       height: 40px;
       text-align: center;
       line-height: 40px;
       position:relative;
       &.active{
           color: #ff1a90;
           font-weight: bold;
           &::before{
               background-color: #ff1a90;
               width: 6px;
               height: 18px;
               position: absolute;
               left: 0px;
                top: 50%;
                transform: translateY(-50%);
                content: "";
           }
       }
   }
   &::-webkit-scrollbar{
       width:0px;
       background: none;
   }
}
</style>
